<template>
  <div class="firstStep">
    <el-row>
      <el-col :span="8">
        <div class="title">
          <el-radio v-model="radio" label="1"></el-radio>
        </div>
        <div class="content">
          <p class="detail"></p>
          <p class="detail"> </p>
          <p class="detail">
            <span class="red"></span>
          </p>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="title">
          <el-radio v-model="radio" label="2"></el-radio>
        </div>
        <div class="content">
          <p class="detail"></p>
          <p class="detail"></p>
          <p class="detail"></p>
          <p class="detail">
            <span class="red"></span>
          </p>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="title">
          <el-radio v-model="radio" label="3"></el-radio>
        </div>
        <div class="content">
          <p class="detail"></p>
          <p class="detail"></p>
          <p class="detail"></p>
          <p class="detail">
            <span class="red"></span>
          </p>
        </div>
      </el-col>
    </el-row>
    <btn-group show-back-btn="2" show-go-btn="2" @btnClick="btnClick" ></btn-group>
  </div>
</template>
<script>
import btnGroup from './components/btnGroup.vue'
export default {
  components: {
    btnGroup
  },
  data () {
    return {
      radio: '1',
      btnActive: 2
    }
  },
  methods: {
    btnClick (val) {
      if (val === 'back') {
        return ''
      } else {
        this.$emit('changeState', { state: 'secondStep', radio: this.radio })
      }
    }
  }
}
</script>
<style lang="less" scoped>
.firstStep {
  margin-top: 20px;
  .el-col {
    border: 1px solid #beeced;
    height: 227px;
    background: #fff;
    &:not(:last-child) {
      margin-right: 10px;
      width: calc(33.3% - 10px);
    }
  }
  .title {
    border-bottom: 2px solid #beeced;
    line-height: 43px;
    text-align: center;
    font-size: 16px;
    color: #3cc;
  }
  .content {
    margin-top: 10px;
    .detail {
      margin: 0 50px;
      font-size: 14px;
      line-height: 25px;
      .red {
        color: #f33 !important;
      }
    }
  }
}
</style>



